<template>
  <form @submit.prevent="validateBeforeSubmit" autocomplete="off">

    <label class="label">Password</label>
    <b-field>
      <p class="control has-icon has-icon-right">
        <input
          name="newPassword"
          v-model="newPassword"
          v-validate="'required|min:8'"
          :class="{'input': true, 'is-danger': errors.has('newPassword') }"
          type="password"
          placeholder="Enter password"
        />
        <i v-show="errors.has('newPassword')" class="fa fa-warning"></i>
        <span
          v-show="errors.has('newPassword')"
          class="help is-danger"
        >Password must have at least 8 characters</span>
      </p>
    </b-field>

    <label class="label">New password</label>
    <b-field
      :type="{'is-danger': errors.has('confirm-password')}"
      :message="[{
                    'You must confirm password' : errors.firstByRule('confirm-password', 'required'),
                    'Password and confirm are not the same'  : errors.firstByRule('confirm-password', 'is')
                }]"
    >
      <b-input
        type="password"
        placeholder="Confirm password"
        v-model="confirmPassword"
        name="confirm-password"
        v-validate="{ required: true, is: newPassword }"
        password-reveal
      />
    </b-field>

    <button type="submit" class="button is-primary">Send</button>
  </form>
</template>


<script>
import axiosRequest from "../axiosRequests";
export default {
  props: ["setName"],
  data() {
    return {
      newPassword: null, // password
      confirmPassword: null
    };
  },
  methods: {
    validateBeforeSubmit() {
      this.$validator.validateAll().then(result => {
        if (result) {
          this.axiosPost();
          return;
        }
        this.$buefy.toast.open({
          message: "Le formulaire n'est pas valide !",
          type: "is-danger",
          position: "is-bottom"
        });
      });
    },
    axiosPost() {
      const formData = {
        password: this.newPassword,
        user: this.$route.params.id
      };
      axiosRequest
        .resetPassword(formData)
        .then(response => {
          this.playSuccess(response);
          this.$router.replace("/connexion");
        })
        .catch(error => {
          this.newPassword = this.confirmPassword = null;
          this.playError(error);
        });
    },
    playError(error) {
      this.$buefy.toast.open({
        message: error,
        type: "is-danger",
        position: "is-bottom",
        duration: 5000
      });
    },
    playSuccess(success) {
      this.$buefy.toast.open({
        message: success,
        type: "is-success",
        position: "is-bottom",
        duration: 5000
      });
    }
  }
};
</script>

<style scoped>
form {
  width: 300px;
  margin: auto;
  margin-top: 50px;
}
label{
  color : white
}
</style>